<template>
  <div class="missions">
    <div class="missions-menu">
      <button @click="$router.push({ name: 'home' })">
        <img src="~@/assets/img/icons/goback.svg" />
      </button>
    </div>

    <div class="missions-list">
      <div class="mission" v-for="mission in missions" :key="mission.id">
        <div class="mission-info">
          <div class="mission-name">
            <h1 @click="openGoalsModal(mission)">
              {{ mission.name }}
            </h1>
            <span
              :class="`mission-track mission-track-${status(mission)}`"
            ></span>
          </div>

          <div
            v-if="mission.completed"
            class="mission-completed mission-tag"
          >
            completed
          </div>
          <div
            v-else-if="!hasRequiredLevel(mission.level_requirement)"
            class="mission-tag mission-unavailable"
          >
            Hasn't reached required level yet
          </div>
          <div
            v-else-if="!mission.completed && !mission.tracking"
            class="mission-available mission-tag"
          >
            Available
          </div>
          <div
            v-else-if="mission.tracking"
            class="mission-tracking mission-tag"
          >
            tracking
          </div>
        </div>
      </div>
    </div>

    <goals
      :mission="selectedMission"
      :goals="selectedMissionGoals"
      :tracked="tracked"
    ></goals>
  </div>
</template>

<script>
import goals from "@/components/Missions/goal.vue";
export default {
  components: {
    goals,
  },
  data() {
    return {
      missions: [],
      selectedMission: {},
      selectedMissionGoals: [],
    };
  },
  methods: {
    async openGoalsModal(mission) {
      this.selectedMissionGoals = [];
      this.selectedMission = mission;
      const r = await this.$http.get(`/mission/${this.selectedMission.id}`);
      if (r.data.success && r.data.goals.length > 0)
        this.selectedMissionGoals = r.data.goals[0].goals;
    },
    hasRequiredLevel(req) {
      return this.user.season.seasonLevel >= req;
    },
    statusMethod(mission) {
      if (!this.hasRequiredLevel(mission.level_requirement))
        return "unavailable";
      else if (!mission.completed && !mission.tracking) return "available";
      else if (mission.completed) return "completed";
      else if (mission.tracking) return "tracking";
      return ""
    },
  },
  async created() {
    const r = await this.$http.get("/mission");
    this.missions = r.data;
    this.openGoalsModal(this.missions[0]);
  },
  computed: {
    user() {
      return this.$store.getters["user/getUserData"];
    },
    tracked() {
      let tracked = 0;
      for (const i in this.missions) {
        if (this.missions[i].tracking) tracked++;
        if (tracked >= 3) return tracked;
      }
      return tracked;
    },
    status(){
      return this.statusMethod
    }
  },
};
</script>

